/* parth Section */
.parth {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    /* height: 100vh; */
    overflow: visible;
    min-height: 100vh;
}

.parth-image {
  display: block;      /* inline-gap à¤¹à¤Ÿà¤¾à¤ */
  width: 100vw;        /* device à¤•à¥€ à¤ªà¥‚à¤°à¥€ width */
  max-width: 100%;     /* zoom à¤®à¥‡à¤‚ à¤­à¥€ à¤•à¤­à¥€ overï¬‚ow à¤¨à¤¾ à¤¹à¥‹ */
  height: auto;        /* aspectâ€‘ratio à¤®à¥‡à¤¨à¥à¤Ÿà¥‡à¤¨ à¤°à¤¹à¥‡ */
  position: relative;  /* à¤…à¤¬ absolute à¤¨à¤¹à¥€à¤‚ */
  z-index: -1;         /* content à¤Šà¤ªà¤° à¤¦à¤¿à¤–à¥‡ */
  filter: brightness(0.75);
}

.parth-content {
  position: absolute;
  /* à¤…à¤ªà¤¨à¥‡ à¤¹à¤¿à¤¸à¤¾à¤¬ à¤¸à¥‡ tweak à¤•à¤°à¥‡à¤‚ */
  top: 10%;            
  left: 52%;
  transform: translate(0, -20%);
  z-index: 1;
  max-width: 600px;
  color: #fff;         /* image à¤•à¥‡ à¤Šà¤ªà¤° text show à¤•à¤°à¤¨à¥‡ à¤•à¥‡ à¤²à¤¿à¤ */
  padding: 30px;
}

.parth-typing {
  min-height: 58px; /* Adjust as per your h1 font-size */
  display: inline-block;
  white-space: nowrap;
}

.parth h1 {
    font-size: 48px;
    font-weight: 700;
    margin-bottom: 20px;
}

.parth p {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 30px;
}

.skills-typing {
  font-size: 24px;
  font-style: italic;
  color: #fa8484;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 10px;
  display: inline-block;
  white-space: nowrap;
}

.skills-typing::after {
  content: '|';
  animation: blink 1s infinite;
  margin-left: 4px;
}

@keyframes blink {
  0%, 50%, 100% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
